
"use client";

import { useEffect, useState } from 'react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import ProtectedRoute from '@/components/ProtectedRoute';
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Plus, SlidersHorizontal, AlertCircle, Clock, CheckCircle, FileText, ChevronRight } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";

export default function GrievanceListPage() {
    const { user } = useAuth();
    const [grievances, setGrievances] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [filterStatus, setFilterStatus] = useState('All');

    useEffect(() => {
        const fetchGrievances = async () => {
            if (!user) return;
            try {
                const token = await user.getIdToken();
                const query = filterStatus !== 'All' ? `?status=${filterStatus}` : '';
                const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api'}/grievances/list${query}`, {
                    headers: {
                        'Authorization': `Bearer ${token}`
                    }
                });

                if (!response.ok) throw new Error('Failed to fetch grievances');

                const data = await response.json();
                setGrievances(data);
            } catch (error) {
                console.error(error);
            } finally {
                setLoading(false);
            }
        };

        fetchGrievances();
    }, [user, filterStatus]);

    const getStatusBadge = (status: string) => {
        switch (status) {
            case 'Resolved':
                return <Badge variant="default" className="bg-green-600 hover:bg-green-700">Resolved</Badge>;
            case 'In Progress':
                return <Badge variant="secondary" className="bg-blue-100 text-blue-800 hover:bg-blue-200">In Progress</Badge>;
            case 'Review':
                return <Badge variant="secondary" className="bg-yellow-100 text-yellow-800 hover:bg-yellow-200">Review</Badge>;
            default:
                return <Badge variant="outline">Submitted</Badge>;
        }
    };

    return (
        <ProtectedRoute allowedRoles={['student', 'authority', 'admin']}>
            <div className="max-w-4xl mx-auto py-10 space-y-8 animate-in fade-in-50">
                <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                    <div>
                        <h1 className="text-3xl font-bold tracking-tight">My Grievances</h1>
                        <p className="text-muted-foreground">Track and manage your submitted reports.</p>
                    </div>
                    <Link href="/grievances/submit">
                        <Button>
                            <Plus className="mr-2 w-4 h-4" /> New Grievance
                        </Button>
                    </Link>
                </div>

                {/* Filter */}
                <div className="flex items-center space-x-2 overflow-x-auto pb-2">
                    <SlidersHorizontal className="w-4 h-4 text-muted-foreground mr-2" />
                    {['All', 'Submitted', 'Review', 'In Progress', 'Resolved'].map((status) => (
                        <Button
                            key={status}
                            variant={filterStatus === status ? "default" : "outline"}
                            size="sm"
                            onClick={() => setFilterStatus(status)}
                            className="rounded-full"
                        >
                            {status}
                        </Button>
                    ))}
                </div>

                {loading ? (
                    <div className="space-y-4">
                        {[1, 2, 3].map(i => (
                            <Card key={i} className="p-6">
                                <div className="space-y-3">
                                    <div className="flex justify-between">
                                        <Skeleton className="h-5 w-24" />
                                        <Skeleton className="h-5 w-20" />
                                    </div>
                                    <Skeleton className="h-6 w-3/4" />
                                    <Skeleton className="h-4 w-full" />
                                </div>
                            </Card>
                        ))}
                    </div>
                ) : grievances.length === 0 ? (
                    <Card className="text-center p-12 border-dashed">
                        <div className="flex flex-col items-center justify-center text-muted-foreground">
                            <div className="w-12 h-12 bg-muted rounded-full flex items-center justify-center mb-4">
                                <FileText className="w-6 h-6" />
                            </div>
                            <h3 className="text-lg font-medium text-foreground">No grievances found</h3>
                            <p className="mb-6">You haven't submitted any grievances yet.</p>
                            <Link href="/grievances/submit">
                                <Button variant="outline">Submit your first grievance</Button>
                            </Link>
                        </div>
                    </Card>
                ) : (
                    <div className="grid gap-4">
                        {grievances.map((grievance: any) => (
                            <Link key={grievance.grievanceId} href={`/grievances/${grievance.grievanceId}`}>
                                <Card className="hover:shadow-md transition-all hover:border-primary/50 group">
                                    <div className="p-6 flex justify-between items-start gap-4">
                                        <div className="space-y-2 flex-1">
                                            <div className="flex items-center gap-2 text-sm text-muted-foreground">
                                                <span>#{grievance.grievanceId.slice(0, 8)}</span>
                                                <span>â€¢</span>
                                                <span>{grievance.createdAt?._seconds ? new Date(grievance.createdAt._seconds * 1000).toLocaleDateString() : 'Just now'}</span>
                                            </div>
                                            <h3 className="text-lg font-semibold group-hover:text-primary transition-colors">
                                                {grievance.category} Issue at {grievance.location}
                                            </h3>
                                            <p className="text-muted-foreground line-clamp-2 text-sm">
                                                {grievance.description}
                                            </p>
                                        </div>
                                        <div className="flex flex-col items-end gap-3">
                                            {getStatusBadge(grievance.status)}
                                            <ChevronRight className="w-5 h-5 text-muted-foreground group-hover:text-primary group-hover:translate-x-1 transition-all mt-auto" />
                                        </div>
                                    </div>
                                </Card>
                            </Link>
                        ))}
                    </div>
                )}
            </div>
        </ProtectedRoute>
    );
}
